<template>
    <div class="card m-2" style="width:15rem;max-height:12rem;">
        <div class=" card-body text-left text-justify h-50" style="text-overflow: ellipsis;overflow : hidden;">
            <h5 class="card-title">{{article.title}}</h5>
            <span>{{article.time}}</span>
            <span class="badge badge-info">{{article.tag}}</span>
            <p class="card-text h-50">{{article.context}}</p>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {}
  },
  props: {
    article: Object
  }
}
</script>



