import Vue from 'vue'
import Router from 'vue-router'
import mySiteRouter from '@/components/mySiteRouter'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mySiteRouter',
      component: mySiteRouter
    }
  ]
})



